/* src/PDFViewer.module.scss */

@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;1,400&display=swap");

.adex-viewer {
    flex-direction: column;
    border-radius: 5px;
    position: relative;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.1);
    /* box-shadow: 2px 2px 5px rgba(0, 0, 0, .1), -2px -2px 5px rgba(0, 0, 0, .1); */
    background-color: #fff;
    font-family: "Open Sans", sans-serif;
}

.adex-viewer * {
    box-sizing: border-box;
    font-family: "Open Sans", sans-serif;
}

.adex-page>div>div,
.adex-page-thumb>div>div {
    display: block !important;
    /* Changed from 'none' to 'block' to ensure text layer is visible */
}

.adex-viewer .adex-topbar {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    display: flex;
    padding: 5px 10px;
    align-items: center;
    justify-content: space-between;
    /* padding-top: 37px; */
}

.adex-viewer .adex-topbar .adex-control-page {
    display: grid;
    grid-template-columns: 30px 30px auto 30px;
    gap: 5px;
    align-items: center;
}

.adex-viewer .adex-topbar .adex-control-page p {
    margin-top: 0px;
    margin-bottom: 0px;
}

.adex-viewer .adex-topbar .adex-control-page input {
    width: 100%;
    max-width: 70px;
    text-align: center;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    outline: 0px !important;
}

.adex-viewer .adex-topbar .adex-control-page p {
    display: flex;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
}

.adex-viewer .adex-topbar .adex-control-options {
    display: grid;
    grid-template-columns: repeat(auto-fill, 30px);
    gap: 5px;
    width: auto;
    min-width: 100px;
}

.adex-viewer .adex-topbar .adex-control-zoom select {
    background: transparent;
    border: none;
    border-radius: 5px;
    padding: 3px 10px;
    cursor: pointer;
}

.adex-viewer .adex-topbar .adex-control-zoom select:focus,
.adex-viewer .adex-topbar .adex-control-zoom select:hover,
.adex-viewer .adex-topbar .adex-control-zoom select:active {
    outline: none;
    background-color: rgba(0, 0, 0, 0.1);
}

.adex-viewer .adex-topbar button,
.adex-viewer .adex-topbar .open-link-btn {
    background: transparent;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.2s all ease-in-out;
    border-radius: 5px;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #4e4e4e;
}

.adex-viewer .adex-topbar button:hover,
.adex-viewer .adex-topbar .open-link-btn:hover,
.adex-viewer .adex-topbar button.active {
    background-color: rgba(0, 0, 0, 0.1);
    color: #000;
}

/* Update the adex-preview-panel styles to support flex layout */
.adex-viewer .adex-preview-panel {
    display: flex;
    position: relative;
}

.adex-left-col {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 6px;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.adex-left-col button {
    background: transparent;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.2s all ease-in-out;
    border-radius: 5px;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #4e4e4e;
}

.adex-left-col button:hover {
    background-color: rgba(0, 0, 0, 0.05);
    color: #000;
}

.adex-left-col button.active {
    background-color: rgba(0, 0, 0, 0.1);
    color: #000;
}

.subsidebar {
    width: 0;
    transition: .2s all ease-in-out;
}

.bookmarks-slide-in .adex-preview-bookmarks.subsidebar {
    width: 100%;
    transition: .2s all ease-in-out;
}

/* Improved text layer styles */
.adex-viewer .adex-preview-panel .textLayer {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    opacity: 0.2;
    line-height: 1.0;
    z-index: 2;
    /* Ensure text layer is above the canvas */
}

.adex-viewer .adex-preview-panel .textLayer>span {
    color: transparent;
    position: absolute;
    white-space: pre;
    cursor: text;
    transform-origin: 0% 0%;
}

.adex-viewer .adex-preview-panel .textLayer .highlight {
    margin: -1px;
    padding: 1px;
    background-color: rgb(180, 0, 170);
    border-radius: 4px;
}

.adex-viewer .adex-preview-panel .textLayer .highlight.begin {
    border-radius: 4px 0px 0px 4px;
}

.adex-viewer .adex-preview-panel .textLayer .highlight.end {
    border-radius: 0px 4px 4px 0px;
}

.adex-viewer .adex-preview-panel .textLayer .highlight.middle {
    border-radius: 0px;
}

.adex-viewer .adex-preview-panel .textLayer .highlight.selected {
    background-color: rgb(0, 100, 0);
}

.adex-viewer .adex-preview-panel .textLayer ::selection {
    background: rgba(0, 0, 255, 0.3);
}

/* Disable text selection if not enabled */
.adex-viewer.disable-text-selection .adex-preview-panel .textLayer {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    pointer-events: none;
    /* Prevent interaction with text layer */
}

.adex-viewer .adex-preview-panel .adex-preview-thumbs {
    max-height: 400px;
    min-height: 400px;
    overflow: auto;
    background-color: #f7f7f7;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    scroll-behavior: smooth;
}

.adex-viewer .adex-preview-panel .adex-preview-thumbs>div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    scroll-behavior: smooth;
}

.adex-viewer .adex-preview-panel .adex-preview-thumbs .adex-page-thumb {
    margin: 5px auto;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.05), -5px -5px 10px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    /* overflow: hidden; */
    border: none;
    background-color: transparent;
    cursor: pointer;
    padding: 0px;
    position: relative;
}

.adex-viewer .adex-preview-panel .adex-preview-thumbs .adex-page-thumb.active:before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(20, 127, 212, 0.1254901961);
    border: 2px solid #147fd4;
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: 99;
    border-radius: 5px;
}

/* Search results sidebar */
.adex-viewer .adex-preview-panel .adex-preview-search {
    max-height: 400px;
    min-height: 400px;
    overflow: auto;
    background-color: #f7f7f7;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    scroll-behavior: smooth;
    width: 100%;
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-results-header {
    padding: 10px 15px;
    /* border-bottom: 1px solid rgba(0, 0, 0, 0.1); */
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0px 0px 10px 0 !important;
}

.adex-search-controls {
    display: flex;
    gap: 10px;
    align-items: center;
}


.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-results-header h3 {
    margin: 0;
    font-size: 14px;
    font-weight: 600;
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-results-count {
    font-size: 12px;
    color: #666;
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-results-list {
    padding: 0;
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-result-item {
    padding: 10px 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-result-item:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-result-item.active {
    background-color: rgba(20, 127, 212, 0.1);
    border-left: 3px solid #147fd4;
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-result-page {
    font-size: 11px;
    color: #666;
    margin-bottom: 5px;
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-result-context {
    font-size: 12px;
    line-height: 1.4;
    color: #333;
    word-break: break-word;
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-result-highlight {
    background-color: rgba(255, 255, 0, 0.4);
    font-weight: bold;
    padding: 0 2px;
    border-radius: 2px;
}

.adex-viewer .adex-preview-panel .adex-preview-search .adex-search-no-results {
    padding: 20px 15px;
    text-align: center;
    color: #666;
    font-size: 13px;
}

.adex-viewer .adex-preview-panel .adex-preview {
    max-height: 400px;
    min-height: 400px;
    overflow: auto;
    scroll-behavior: smooth;
    padding-top: 10px;
    position: relative;
}

.adex-viewer .adex-preview-panel .adex-preview>div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    scroll-behavior: smooth;
}

.adex-viewer .adex-preview-panel .adex-preview .adex-page {
    margin: 10px auto;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.05), -5px -5px 10px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    background-color: white;
    /* Add this line */
}

/* Also ensure the canvas has a white background */
.adex-viewer .adex-preview-panel .adex-preview .adex-page canvas {
    background-color: white !important;
}

/* Remove the page-specific rotation controls styles */
.adex-page-rotation-controls {
    display: none;
}

/* Ensure rotated pages display correctly */
.adex-page canvas {
    transition: transform 0.3s ease;
}

.adex-viewer .adex-power-row {
    right: 0;
    bottom: 0;
    width: 100%;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: end;
    padding: 7px 15px;
}

.adex-viewer .adex-powered {
    max-width: 70px;
}

.adex-viewer.fullScreenView {
    background-color: #fff;
}

.adex-viewer.fullScreenView .adex-topbar {
    padding-top: 5px;
}

.adex-viewer.fullScreenView .adex-preview-panel .adex-preview-thumbs,
.adex-viewer.fullScreenView .adex-preview-panel .adex-preview-search,
.adex-viewer.fullScreenView .adex-preview-panel .adex-preview {
    max-height: calc(100vh - 81px);
    min-height: calc(100vh - 81px);
}

.adex-viewer.fullScreenView .adex-control-options {
    width: 30px !important;
}

.adex-viewer.fullScreenView .open-link-btn {
    display: none !important;
}

.dev-ab {
    display: flex;
}

/* Sidebar slide animations */
.thumbs-slide-in .adex-preview-panel {
    overflow: hidden;
    /* grid-template-columns: 30px 180px auto; */
    transition: 0.2s all ease;
}

.thumbs-slide-in .adex-preview-panel .adex-preview-thumbs {
    transform: translateX(0px);
    transition: 0.2s all ease;
}

.thumbs-slide-out .adex-preview-panel {
    overflow: hidden;
    grid-template-columns: 44px 0px auto;
    transition: 0.2s all ease;
}

.thumbs-slide-out .adex-left-col {
    border-right: unset !important;
}

.thumbs-slide-out .adex-preview-panel .adex-preview-thumbs {
    /* transform: translateX(-180px); */
    transition: 0.2s all ease;
}

/* Search sidebar slide animations */
.search-slide-in .adex-preview-panel {
    overflow: hidden;
    /* grid-template-columns: 30px 250px auto; */
    transition: 0.2s all ease;
}

.search-slide-in .adex-preview-panel .adex-preview-search {
    transform: translateX(0px);
    transition: 0.2s all ease;
}

.search-slide-out .adex-preview-panel {
    overflow: hidden;
    /* grid-template-columns: 30px 180px 0px auto; */
    transition: 0.2s all ease;
}

.search-slide-out .adex-preview-panel .adex-preview-search {
    /* transform: translateX(-250px); */
    transition: 0.2s all ease;
}

/* When both sidebars are closed */
.thumbs-slide-out.search-slide-out .adex-preview-panel {
    /* grid-template-columns: 30px 0px auto; */
}

@keyframes placeHolderShimmer {
    0% {
        background-position: -468px 0;
    }

    100% {
        background-position: 468px 0;
    }
}

.adex-thumb-loader span {
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: placeHolderShimmer;
    animation-timing-function: linear;
    display: block;
    height: 170px;
    width: 120px;
    background: #f6f7f8;
    background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
    background-size: 1000px 170px;
    margin: 10px auto;
    border-radius: 5px;
    position: relative;
    overflow: hidden;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.adex-preview-loader span {
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: placeHolderShimmer;
    animation-timing-function: linear;
    display: block;
    height: 800px;
    width: 80%;
    background: #f6f7f8;
    background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
    background-size: 1000px 170px;
    margin: 10px auto;
    border-radius: 5px;
    position: relative;
    overflow: hidden;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.adex-pdf-meta-info {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(2px);
}

.adex-preview-panel {
    position: relative;
}

.adex-pdf-meta-info {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(2px);
}

.adex-preview-panel {
    position: relative;
}

.adex-meta-panel {
    background: #fff;
    max-width: 300px;
    max-height: 300px;
    overflow: auto;
    border-radius: 5px;
}

.adex-pdf-meta-info {
    display: flex;
    justify-content: center;
    align-items: center;
}

.adex-pdf-meta-info-content {
    background: #f7f7f7;
}

ul.adex-pdf-meta-info-list {
    list-style-type: none;
    padding: 15px 15px;
    display: flex;
    flex-direction: column;
    gap: 6px;
    font-size: 12px;
    margin: 0px;
}

.adex-pdf-meta-info-header {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    position: sticky;
    top: 0;
    background: #fff;
    z-index: 2;
    align-items: center;
    padding-right: 0;
}

.adex-pdf-meta-info-header strong {
    max-width: 200px;
    height: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 13px;
}

.adex-pdf-meta-info-header strong svg {
    margin-top: 1px;
    float: left;
    margin-right: 10px;
}

.adex-pdf-meta-info-header button,
.adex-power-row button {
    background: transparent;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.2s all ease-in-out;
    border-radius: 5px;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.adex-pdf-meta-info-header button:hover,
.adex-power-row button:hover {
    background-color: rgba(0, 0, 0, 0.1);
    color: #000;
}

.adex-viewer .adex-power-row {
    justify-content: space-between;
    align-items: center;
}

.adex-power-row p {
    margin: 0px;
    font-size: 12px;
}

.adex-power-row span {
    color: red;
}

/* Mobile styles */
.adex-mobile .adex-topbar {
    flex-wrap: wrap;
    gap: 5px;
}

.adex-mobile .adex-topbar .adex-control-page {
    grid-template-columns: 30px 30px auto 30px;
    width: 100%;
}

.adex-mobile .adex-topbar .adex-control-zoom,
.adex-mobile .adex-topbar .adex-control-options {
    margin-left: auto;
}

.adex-mobile .adex-preview-panel .adex-preview,
.adex-mobile .adex-preview-panel .adex-preview-thumbs,
.adex-mobile .adex-preview-panel .adex-preview-search {
    max-height: 300px;
    min-height: 300px;
}

.adex-mobile.fullScreenView .adex-preview-panel .adex-preview,
.adex-mobile.fullScreenView .adex-preview-panel .adex-preview-thumbs,
.adex-mobile.fullScreenView .adex-preview-panel .adex-preview-search {
    max-height: calc(100vh - 81px);
    min-height: calc(100vh - 81px);
}

/* Simplified toolbar for mobile */
.adex-mobile .adex-topbar .adex-control-page p {
    font-size: 12px;
}

.adex-mobile .adex-topbar .adex-control-page input {
    max-width: 50px;
}

/* Make sure the PDF fits on mobile screens */
.adex-mobile .adex-preview-panel .adex-preview .adex-page {
    max-width: 100%;
    overflow: auto;
}

.adex-mobile .adex-preview-panel .adex-preview .adex-page canvas {
    max-width: 100%;
    height: auto !important;
}

/* React-PDF text layer styles */
.react-pdf__Page__textContent {
    border: 1px solid transparent;
    box-sizing: border-box;
    cursor: text;
    left: 0;
    line-height: 1;
    opacity: 0.2;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    user-select: text;
    z-index: 2;
}

.react-pdf__Page__textContent span {
    color: transparent;
    cursor: text;
    position: absolute;
    white-space: pre;
}

.react-pdf__Page__textContent ::selection {
    background-color: rgba(0, 0, 255, 0.3);
}

/* Ensure text layer is visible and selectable */
.adex-page .react-pdf__Page__textContent {
    display: block !important;
    pointer-events: auto !important;
}

/* Disable text selection when not enabled */
.disable-text-selection .react-pdf__Page__textContent {
    pointer-events: none !important;
    user-select: none !important;
    -webkit-user-select: none !important;
    -moz-user-select: none !important;
    -ms-user-select: none !important;
}

/* Print dialog styles - Fix visibility issues */
.adex-print-dialog {
    max-width: 400px;
    max-height: 400px;
    z-index: 1001;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.adex-pdf-meta-info {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(2px);
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.adex-print-dialog-content {
    padding: 15px 20px;
    background: #fff;
}

.adex-print-range {
    margin-bottom: 20px;
}

.adex-print-range label {
    display: block;
    font-weight: bold;
    margin-bottom: 10px;
}

.adex-print-range-inputs {
    display: flex;
    gap: 15px;
}

.adex-print-range-from,
.adex-print-range-to {
    display: flex;
    align-items: center;
    gap: 5px;
}

.adex-print-range-from span,
.adex-print-range-to span {
    font-size: 12px;
}

.adex-print-range-from input,
.adex-print-range-to input {
    width: 60px;
    padding: 5px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
}

.adex-print-buttons {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    margin-top: 20px;
}

.adex-print-buttons button {
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    transition: all 0.2s ease;
    width: auto;
    height: auto;
}

.adex-print-cancel {
    background-color: #f1f1f1;
    border: 1px solid #ddd;
    color: #333;
}

.adex-print-cancel:hover {
    background-color: #e5e5e5;
}

.adex-print-confirm {
    background-color: #147fd4;
    border: 1px solid #0e6cb4;
    color: white;
}

.adex-print-confirm:hover {
    background-color: #0e6cb4;
}

/* Print mode styles */
@media print {
    body * {
        visibility: hidden;
    }

    .adex-viewer.adex-printing,
    .adex-viewer.adex-printing * {
        visibility: visible;
    }

    .adex-viewer.adex-printing {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: auto;
        overflow: visible;
        border: none !important;
        margin: 0 !important;
        padding: 0 !important;
    }

    .adex-viewer.adex-printing .adex-topbar,
    .adex-viewer.adex-printing .adex-power-row,
    .adex-viewer.adex-printing .adex-preview-thumbs,
    .adex-viewer.adex-printing .adex-preview-search,
    .adex-viewer.adex-printing .adex-pdf-meta-info {
        display: none !important;
    }

    .adex-viewer.adex-printing .adex-preview-panel {
        display: block !important;
        grid-template-columns: auto !important;
        margin: 0 !important;
        padding: 0 !important;
    }

