.adex-annotation-actions {
    display: flex;
    justify-content: flex-end;
    margin-top: 15px;
}

.adex-annotation-goto {
    padding: 6px 12px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    background: #147fd4;
    color: white;
    cursor: pointer;
    font-size: 12px;
}

.adex-annotation-goto:hover {
    background-color: #0e6cb4;
}

.adex-annotation {
    transition: transform 0.2s ease;
}

.adex-note-annotation:hover {
    transform: scale(1.2);
}

.adex-highlight-annotation:hover {
    opacity: 0.5;
}

/* Fullscreen adjustments for annotations */
.adex-viewer.fullScreenView .adex-preview-panel .adex-preview-annotations {
    max-height: calc(100vh - 81px);
    min-height: calc(100vh - 81px);
}

/* Mobile adjustments for annotations */
.adex-mobile .adex-preview-panel .adex-preview-annotations {
    max-height: 300px;
    min-height: 300px;
}

.adex-mobile.fullScreenView .adex-preview-panel .adex-preview-annotations {
    max-height: calc(100vh - 81px);
    min-height: calc(100vh - 81px);
}

/* Annotation sidebar slide animations */
.annotations-slide-in .adex-preview-panel {
    overflow: hidden;
    transition: 0.2s all ease;
}

.annotations-slide-in .adex-preview-panel .adex-preview-annotations {
    transform: translateX(0px);
    transition: 0.2s all ease;
}

.annotations-slide-out .adex-preview-panel {
    overflow: hidden;
    transition: 0.2s all ease;
}

.annotations-slide-out .adex-preview-panel .adex-preview-annotations {
    transition: 0.2s all ease;
}

/* Add these styles to improve the appearance of annotations */

.adex-annotation.adex-drawing-annotation {
    overflow: visible;
}

.adex-annotation.adex-drawing-annotation path {
    stroke-linecap: round;
    stroke-linejoin: round;
}

.adex-current-drawing path {
    stroke-linecap: round;
    stroke-linejoin: round;
}

/* Make the annotation tools more visible when active */
.adex-annotation-tool.active {
    background-color: rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
    .adex-viewer .adex-topbar {
        display: grid;
        grid-template-columns: 2fr 1 fr;
        align-items: center;
    }

    .adex-control-page {
        grid-column: span 2;
        justify-content: start;
    }

    .adex-control-options {
        margin-top: 10px;
    }

}

.adex-pdf-meta-info.adex-locales-panel .adex-meta-panel {
    width: 80%;
    max-width: unset;
    max-height: unset;
    height: 70%;
}

.adex-pdf-meta-info.adex-locales-panel .adex-pdf-meta-info-content {
    min-height: calc(100% - 50px);
}

ul.adex-language-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 150px);
    list-style-type: none;
    padding: 20px;
    gap: 10px;
}

ul.adex-language-list li {
    background: #fff;
    padding: 10px 20px;
    font-size: 13px;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, .1);
    cursor: pointer;
    transition: .2s all ease;
}

.adex-left-option {
    display: flex;
    gap: 5px;
}

ul.adex-language-list li.adex-language-option.active-lang {
    background-color: rgba(20, 127, 212, 0.1);
    border-top: 3px solid #147fd4;
}

ul.adex-language-list li:hover {
    box-shadow: 2px 2px 5px rgba(20, 127, 212, 0.1), -2px -2px 5px rgba(20, 127, 212, 0.1)
}